$gap: 20px;

.photos {
  display: flex;
  justify-content: space-between;

  &__img {
    width: calc(50% - #{$gap/2});
    height: 260px;
    object-fit: cover;
    object-position: center;
  }

  &_wide_right {
    .photos__img:first-child {
      width: calc(33% - #{$gap/2});
    }

    .photos__img:last-child {
      width: calc(67% - #{$gap/2});
    }
  }

  &_wide_left {
    .photos__img:first-child {
      width: calc(67% - #{$gap/2});
    }

    .photos__img:last-child {
      width: calc(33% - #{$gap/2});
    }
  }
}
